
import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Spinner, Col, FormGroup, Form, Label, Input, Card } from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import * as APP_CONFIG from '../../config/config';
import axios from 'axios';


const SubscriptionDataButtons = (props) => {
    const {
        buttonLabelView,
        className
    } = props;

    const [modal, setModal] = useState(false);
    const [modal2, setModal2] = useState(false);
    const [modal3, setModal3] = useState(false);



    // Intialize modals
    const toggle = () => setModal(!modal);
    const toggleEdit = () => setModal2(!modal2);
    const toggleDelete = () => setModal3(!modal3);

    let _that = this;

    function closeModal(e){
        e.preventDefault();
        setModal(false)
    }

    function subscriptionDataEdit(e){
        e.preventDefault();

        const currentUser = JSON.parse(localStorage.getItem("currentUser"));
        const createdBy = currentUser?.role;

        let formID = document.querySelector("#subscriptionDataFormId");
        const formData = new FormData(formID);

        var campaignType = formData.get('campaignType');
        var msisdn = formData.get('msisdn');
        var channelId = formData.get('channelId');
        var campaignId = formData.get('campaignId');
        var campaignRemarks = formData.get('campaignRemarks');

        var request = new XMLHttpRequest();

        request.onreadystatechange = function () {
            if (this.readyState === 4 & this.status === 200) {
                alert('Uploaded!');
            }
        };

        // props.selectedSubscriptionId
        let subscriptionDataEditUrl = "?" + "type" + "=" + campaignType + "&" + "msisdn" + "=" + msisdn + "&" + "channelId" + "=" + channelId + "&" + "campaignId" + "=" + campaignId;
        axios.put(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_CREATE_SUBSCRIPTION + subscriptionDataEditUrl,
        {
            remarks: campaignRemarks
        },
                {
                    headers: {

                        ContentType: "application/json",
                        Authorization: currentUser.token
                    }
                }).then(function (res) {
                    //handle success
                    toast.success('Added Subscription Successfully');
                    setTimeout(function () {
                        window.location.reload();
                    }, 1000)
                }).catch(function (error) {
                    if (error.response) {
                        /*
                         * The request was made and the server responded with a
                         * status code that falls out of the range of 2xx
                         */
                        toast.error('Error Adding Subscription **');
                    } else if (error.request) {
                        /*
                         * The request was made but no response was received, `error.request`
                         * is an instance of XMLHttpRequest in the browser and an instance
                         * of http.ClientRequest in Node.js
                         */
                        toast.error('Error Adding Subscription ***');
                    } else {
                        // Something happened in setting up the request and triggered an Error
                        toast.error('Error Adding Subscription ****');
                    }
                });


    }
    return (

        <div>
            {/* Button in table for View, Edit, Delete channel */}
            <a className="link" style={{ color: '#12aee0' }} onClick={toggle} >View</a>

            {/* <button className="btn btn-lg" onClick={toggleEdit} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 10, paddingRight: 10, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>{props.iconId}Edit</button>

            <button className="btn btn-lg" onClick={toggleDelete} style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}> Delete</button> */}

            {/*  View Subscription and Edit Details */}
            <Modal isOpen={modal} toggle={toggle} className={className} size="lg">
                <Form id="subscriptionDataFormId" onSubmit={subscriptionDataEdit} >
                    <ModalHeader toggle={toggle}>View</ModalHeader>
                    <ModalBody>
                        <br />
                        <div className="container">
                            <FormGroup row>
                                <Label htmlFor="campaignType" sm="12" md={{ size: 2, offset: 2 }} style={{ color: '#E60000' }}><a style={{ color: 'black' }}>*</a>Type:</Label>
                                <Col sm="12" md={{ size: 6, offset: 0 }} style={{ paddingLeft: '15px' }}>
                                    <Input type="text" name="campaignType" id="campaignType" defaultValue={props?.selectedSubscriptionType} required />
                                </Col>
                            </FormGroup>

                            <FormGroup row>
                                <Label htmlFor="msisdn" sm="12" md={{ size: 2, offset: 2 }} style={{ color: '#E60000' }}><a style={{ color: 'black' }}>*</a>MSISDN:</Label>
                                <Col sm="12" md={{ size: 6, offset: 0 }} style={{ paddingLeft: '15px' }}>
                                    <Input type="text" name="msisdn" id="msisdn" defaultValue={props?.selectedSubscriptionMsisdn} required />
                                </Col>
                            </FormGroup>

                            <FormGroup row>
                                <Label htmlFor="channelId" sm="12" md={{ size: 2, offset: 2 }} style={{ color: '#E60000' }}><a style={{ color: 'black' }}>*</a>Channel:</Label>
                                <Col sm="12" md={{ size: 6, offset: 0 }}>
                                    <Input type="text" name="channelId" id={props?.selectedSubscriptionChannel?.id} defaultValue={props?.selectedSubscriptionChannel?.label || null} required >
                                    </Input>
                                </Col>
                            </FormGroup>

                            <FormGroup row>
                                <Label htmlFor="campaignId" sm="12" md={{ size: 2, offset: 2 }} style={{ color: '#E60000' }}><a style={{ color: 'black' }}>*</a>Campaign:</Label>
                                <Col sm="12" md={{ size: 6, offset: 0 }}>
                                    <Input type="text" name="campaignId" id={props?.selectedSubscriptionChannel?.id} defaultValue={props?.selectedSubscriptionCampaign?.label || null} required>
                                    </Input>
                                </Col>
                            </FormGroup>

                            <FormGroup row>
                                <Label htmlFor="campaignRemarks" sm="12" md={{ size: 2, offset: 2 }} style={{ color: '#E60000' }}> Remarks: </Label>
                                <Col sm="12" md={{ size: 6, offset: 0 }}>
                                    <Input type="textarea" name="campaignRemarks" id="campaignRemarks" defaultValue={props?.selectedSubscriptionRemarks || null} >
                                    </Input>
                                </Col>
                            </FormGroup>
                        </div>

                    </ModalBody>
                    <ModalFooter>

                        <div className="btn-group">
                            <a style={{ paddingRight: '5px' }}>
                                <button className="btn btn-lg" onClick={closeModal} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>Close</button>{' '}
                            </a>

                            <button type="submit" onClick={subscriptionDataEdit} className="btn btn-lg" style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 10, paddingRight: 10, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Edit</button>
                        </div>
                    </ModalFooter>
                </Form>
            </Modal>

            {/* Create modal for Edit */}
            <Modal isOpen={modal2} toggleEdit={toggleEdit} className={className}>
                <ModalHeader toggleEdit={toggleEdit}></ModalHeader>
                <ModalBody>
                    {/* <div style={{ float: 'right'}}>
                        <Spinner color="danger" />
                        <Spinner color="light" />
                        <Spinner color="dark" />
                    </div> */}
                    <br />

                    {props.selectedSubscriptionId}
                </ModalBody>
                <ModalFooter>
                    <div className="btn-group">
                        <button className="btn btn-lg" onClick={toggleEdit} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>Cancel</button>{' '}
                        <button className="btn btn-lg" type="submit" style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 10, paddingRight: 20, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Add</button>
                    </div>
                </ModalFooter>
            </Modal>


            {/* Create modal for delete */}

            <Modal isOpen={modal3} toggle={toggleDelete} className={className}>
                <ModalHeader toggle={toggleDelete}>Delete</ModalHeader>
                <ModalBody>
                    {/* <div style={{ float: 'right'}}>
                        <Spinner color="danger" />
                        <Spinner color="light" />
                        <Spinner color="dark" />
                    </div> */}
                    <br />
                    {props.selectedSubscriptionId}


                </ModalBody>
                <ModalFooter>
                    <div className="btn-group">
                        <button className="btn btn-lg" onClick={toggleDelete} style={{ backgroundColor: '#000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 5, paddingRight: 5, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }}>Cancel</button>{' '}
                        <button className="btn btn-lg" type="submit" style={{ backgroundColor: '#E60000', color: '#fff', borderColor: 'green', borderRadius: 10, paddingLeft: 10, paddingRight: 20, paddingTop: 0, paddingBottom: 0, textTransform: 'unset' }} >Add</button>
                    </div>
                </ModalFooter>
            </Modal>
        </div>
    );
}

export default SubscriptionDataButtons;