import React from 'react';
import { MDBContainer, MDBDataTable, MDBTable, MDBTableBody, MDBTableHead } from 'mdbreact';
import $ from 'jquery';


const scriptDataTables = ({ posts }) => {

    const data = {
        columns: [
            {
                label: '#',
                field: 'id',
                sort: 'disabled',
                width: 1
            },
            {
                label: 'Channel',
                field: 'channel',
                sort: 'asc',
                width: '40'
            },
            {
                label: 'Campaign',
                field: 'campaign',
                sort: 'dsc',
                width: 40
            },
            {
                label: 'MSISDN',
                field: 'msisdn',
                sort: 'dsc',
                width: 40
            },
            {
                label: 'Created On',
                field: 'createdOn',
                sort: 'asc',
                width: 40
            },
            {
                label: 'Expires On',
                field: 'expiresOn',
                sort: 'asc',
                width: 40
            },
            {
                label: 'Actions',
                field: 'action',
                sort: 'disabled',
                width: 50,
                height: 5
            }
        ],
        rows: posts
    };

    return (
        <MDBContainer>
            <MDBTable 
                scrollY
                maxHeight="300px"
                id="idforSubscription"
                small = {true}
                searching="false"
                paging="true"
                theadColor = "white" 
                responsiveXl 
                responsiveSm  
                striped 
                data={data } 
                sortable="true"
                bordered
                >
                <MDBTableHead columns={data.columns} />
                <MDBTableBody rows={data.rows} />
            </MDBTable>
        </MDBContainer>

        // <table id="idforSubscription" className="table table-striped table-bordered table-sm " cellspacing="0"
        //     width="100%" >
        //     <thead>
        //         <tr>
        //             <th>#</th>
        //             <th>Channel</th>
        //             <th>Campaign</th>
        //             <th>MSISDN</th>
        //             <th>Created On</th>
        //             <th>Expires On</th>
        //             <th>Actions</th>
        //         </tr>
        //     </thead>
        //     <tbody>
        //         {
        //             posts.map((post, id) => {
        //                 return (
        //                     <tr>
        //                         <td>{post?.id}  </td>
        //                         <td>{post.channel}  </td>
        //                         <td>{post?.campaign}  </td>
        //                         <td>{post?.msisdn}  </td>
        //                         <td>{post?.createdOn}  </td>
        //                         <td>{post?.expiresOn}  </td>
        //                         <td>{post?.action}  </td>
        //                     </tr>
        //                     )
        //             })
        //         }
        //     </tbody>
        // </table>


    );
};

export default scriptDataTables;