import React, { Component, Fragment } from 'react';
import SubscriptionDataTables from './SubscriptionDataTables';
import { MDBIcon, MDBBtn, MDBBtnGroup } from 'mdbreact';
import { Spinner } from 'reactstrap';
import "../Channel/channel.css";
import axios from 'axios';
import * as APP_CONFIG from '../../config/config';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import $ from 'jquery';
import DataTable from 'datatables.net';
import SubscriptionDataButtons from './SubscriptionDataButtons';
import CreatableSelect from 'react-select/creatable';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Col, FormGroup, Form, Label, Input, Card } from 'reactstrap';
import AddSubscription from "./AddSubscription";
import { toast } from 'react-toastify';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { parseTwoDigitYear } from 'moment';
import FormText from 'reactstrap/lib/FormText';
import swal from 'sweetalert2'

const logo = require('../../assets/img/brand/dnd_vodafone.png');



const moment = require('moment');

let loading = <Spinner color="danger" />;

export class ExportExcel extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ProductData: [],
            FilterData: [],
            channelData: [],
            campaignData: [],
            selectedSubscriptionId: "",
            loading: true,
            currentUserErrorCode: null,
            startDate: null,
            endDate: null

        }
    }

    componentDidMount() {

        let _that = this;

        const currentUser = JSON.parse(localStorage.getItem("currentUser"));

        var request = new XMLHttpRequest();





        this.setState({ loading: true }, () => {

            // Get Channel ID and Name
            axios.get(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_GET_CHANNEL,
                {
                    headers: {
                        ContentType: "application/json",
                        Authorization: currentUser.token
                    }

                }
            ).then(res => {

                let postChannelResults = res?.data?.channels;

                this.setState({
                    channelData: postChannelResults,
                })

            })

            // Get Campaign Data ID and Name
            axios.get(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_GET_CAMPAIGN,
                {
                    headers: {
                        ContentType: "application/json",
                        Authorization: currentUser.token
                    }

                }
            ).then(res => {

                let postCampaignResults = res?.data?.campaigns;
                // ("postCampaignResults Data ==>", postCampaignResults);
                this.setState({
                    campaignData: postCampaignResults
                })

            })

            axios.get(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_GET_SUBSCRIPTION,
                {
                    headers: {
                        ContentType: "application/json",
                        Authorization: currentUser.token,

                    }
                }
            )
                .then(res => {

                    let currentUserErrorCode = res?.data?.errCode;

                    let postSubscriptionResults = res?.data?.subscriptions || [];

                    let postSubscriptions = postSubscriptionResults;
                    // console.log("Sub Data ==>", postSubscriptions);
                    const postList = postSubscriptions?.length ? (
                        postSubscriptions.map((post, id) => {

                            let dateCreated = new Date(post.createdOn);
                            let dateUpdated = new Date(post.updatedOn);
                            return (
                                {
                                    id: id + 1,
                                    channel: post?.channel?.label,
                                    campaign: post?.campaign?.label,
                                    msisdn: (post?.msisdn),
                                    createdOn: moment(post?.createdOn).format('DD/MM/YYYY'),
                                    expiresOn: moment(post?.expiresOn).format('DD/MM/YYYY'),
                                    action:
                                        <Fragment>
                                            <MDBBtnGroup>
                                                <SubscriptionDataButtons selectedSubscriptionId={post?.id} selectedSubscriptionType={post?.subscrptionType} selectedSubscriptionRemarks={post?.remarks} selectedSubscriptionMsisdn={post?.msisdn} selectedSubscriptionChannel={post?.channel} selectedSubscriptionCampaign={post?.campaign} />
                                            </MDBBtnGroup>
                                        </Fragment>
                                }
                            )
                        })
                    ) : []

                    _that.setState(
                        {
                            ProductData: postList,
                            currentUserErrorCode: currentUserErrorCode
                        }
                    )
                })
        });

    }


    // Apply filter to All Subscriptions
    applyFilter = (e) => {

        let data = {};
        let _that = this;
        let formID = document.querySelector("#applyfilter");
        const formData = new FormData(formID);


        let channelTags = formData.getAll('channelTags');
        let channelTag = channelTags.toString();
        let campaignTags = formData.getAll('campaignTags');
        let campaignTag = campaignTags.toString();
        let dateSelectedFrom = formData.get('dateFrom');
        let dateSelectedTo = formData.get('dateTo');

        data["channelTag"] = channelTag;
        data["campaignTag"] = campaignTag;
        data["dateSelectedFrom"] = dateSelectedFrom;
        data["dateSelectedTo"] = dateSelectedTo;

        let dataChannelTag = [];
        dataChannelTag = data.channelTag;
        // let tttt = document.getElementById('#channelTag') ;
        // console.log("Apply Filter channelTag==>", channelTags);
        // console.log("Apply Filter campaignTagTag==>", campaignTags);
        // console.log("Apply Filter campaignTag==>", data.campaignTag);
        // console.log("Apply Filter Data==>", data);

        if (dateSelectedTo < dateSelectedFrom & dateSelectedTo === "") {

            toast.error("Date To - should be greater");
        }
        if (dateSelectedFrom === "") {

            toast.error("Select Date Range For Filter");
        }

        const currentUser = JSON.parse(localStorage.getItem("currentUser"));
        e.preventDefault();

        // Get Subscriptions Campaigns
        axios.get(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_GET_CAMPAIGN,
            {
                headers: {
                    ContentType: "application/json",
                    Authorization: currentUser.token
                }

            }
        ).then(res => {

            let postCampaignResults = res?.data?.campaigns;
            this.setState({
                campaignData: postCampaignResults
            })

        })

        let subscriptionsUrl = "?" + "channelIds" + "=" + channelTag + "&" + "campaignIds" + "=" + campaignTag + "&" + "from" + "=" + dateSelectedFrom + "&" + "to" + "=" + dateSelectedTo;

        axios.get(APP_CONFIG[APP_CONFIG.REACT_APP_ENVIRONMENT].API_GET_SUBSCRIPTION_FILTRATION + subscriptionsUrl,
            {
                headers: {
                    ContentType: "application/json",
                    Authorization: currentUser.token,
                }
            }
        )
            .then(res => {
                // let postSubscriptionResult = res?.data?.data || [];

                let postSubscriptionResult = res?.data?.data[0] || [];
                let postSubscriptionResultList = postSubscriptionResult.subscriptions.map(function (data) {
                    // console.log('postSubscriptionResultList', data)
                    return res.data === null ? null : data || [];
                });

                const postList = postSubscriptionResultList ? (
                    postSubscriptionResultList.map((post, id) => {
                        // console.log("post==>", post);

                        // let dateCreated = new Date(post.createdOn);
                        // let dateUpdated = new Date(post.updatedOn);
                        return (
                            (
                                null === null
                            ) ?
                                {
                                    id: id + 1,
                                    channel: post?.channel?.label,
                                    campaign: post?.campaign?.label,
                                    msisdn: post?.msisdn,
                                    createdOn: moment(post?.createdOn).format('DD/MM/YYYY'),
                                    expiresOn: moment(post?.expiresOn).format('DD/MM/YYYY'),
                                    action:
                                        <Fragment>
                                            <MDBBtnGroup>
                                                <SubscriptionDataButtons selectedSubscriptionId={post?.id} selectedSubscriptionType={post?.subscrptionType} selectedSubscriptionRemarks={post?.remarks} selectedSubscriptionMsisdn={post?.msisdn} selectedSubscriptionChannel={post?.channel} selectedSubscriptionCampaign={post?.campaign} />
                                            </MDBBtnGroup>
                                        </Fragment>

                                } : []
                        )
                    })
                ) : []

                _that.setState(
                    { ProductData: postList }
                )
            }).catch(function (error) {
                if (error.response) {
                    /*
                     * The request was made and the server responded with a
                     * status code that falls out of the range of 2xx
                     */
                    toast.error('Error Filtering Subscriptions **');
                } else if (error.request) {
                    /*
                     * The request was made but no response was received, `error.request`
                     * is an instance of XMLHttpRequest in the browser and an instance
                     * of http.ClientRequest in Node.js
                     */
                    toast.error('Error Filtering Subscriptions ***');
                } else {
                    // Something happened in setting up the request and triggered an Error
                    
                    toast.error('Error Filtering Subscriptions ****');
                }
            });

    }
    // unhide Date Picker To
    selectedDateTo(e) {
        // e.preventDefault();

        if (document.querySelector("#dateFrom")) {

            document.querySelector('#hideDateTo').style.display = 'block';
        }

    }
    handleChangeFrom = (e) => {
        this.setState({
            startDate: e
        });
    };

    handleChange = (e) => {
        this.setState({
            endDate: e
        });
    };


    render() {
        // toLowerCase().replace(/\W/g, ''
        const createOption = (label, dataId) => ({
            label,
            value: dataId,
        });
        const channelOptions =
            this.state?.channelData?.map((post, id) => {
                return createOption(post?.label, post?.id)
            });

        const campaignOptions =
            this.state?.campaignData?.map((post, id) => {
                return createOption(post?.label, post.id)
            });

        const today = moment();
        if (this.state?.currentUserErrorCode == "2") {
            window.location.href = "/login";
            localStorage.removeItem("currentUser")
        }

        return (
            <div className="container">
                <Card style={{ backgroundColor: "#a8a7a3", padding: "10px 0 10px 10px", width: '100%' }}>

                    <AddSubscription channelData={this.state?.channelData} campaignData={this.state?.campaignData} />


                    <br />
                    <br />
                    <br />
                    <form id="applyfilter" onSubmit={this.applyFilter}>
                        <FormGroup row>
                            <label htmlFor="channelTags" ></label>
                            <Col sm="12" md={{ size: 3, offset: 0 }}>
                                <CreatableSelect
                                    isMulti
                                    name="channelTags"
                                    id="channelTags"
                                    placeholder="Select Channel"
                                    options={channelOptions} />
                            </Col>
                            <label htmlFor="campaignTags" ></label>
                            <Col sm="12" md={{ size: 3, offset: 0 }} style={{ clear: "both" }}>
                                <CreatableSelect
                                    isMulti
                                    name="campaignTags"
                                    id="campaignTags"
                                    placeholder="Select Campaign"
                                    options={campaignOptions} />
                            </Col>


                            <Col sm="12" md={{ size: 2, offset: 0 }} style={{ clear: "both" }}>

                                <DatePicker
                                    name="dateFrom"
                                    id="dateFrom"
                                    placeholderText="Date From"
                                    selected={this.state?.startDate}
                                    onChange={(e) => { this.handleChangeFrom(e); this.selectedDateTo() }}
                                    dateFormat="dd/MM/yyyy"
                                    peekNextMonth
                                    showMonthDropdown
                                    showYearDropdown
                                    dropdownMode="select"
                                    withPortal
                                    className="red-border"

                                />

                                <div id="hideDateTo" style={{ display: 'none', clear: "both" }} >
                                    <label htmlFor="dateTo" style={{ color: "#fff" }}></label>
                                    <DatePicker
                                        name="dateTo"
                                        id="dateTo"
                                        placeholderText="Date To"
                                        selected={this.state?.endDate}
                                        onChange={(e) => { this.handleChange(e) }}
                                        dateFormat="dd/MM/yyyy"
                                        peekNextMonth
                                        showMonthDropdown
                                        showYearDropdown
                                        dropdownMode="select"
                                        withPortal
                                        className="red-border"
                                    />
                                    {/* <Input type="date" name="dateTo" id="dateTo" placeholder="To" /> */}
                                </div>
                            </Col>
                            <br />
                                {/* <Input
                                    type="date"
                                    date-date=""
                                    data-date-format="dd mm yyyy"
                                    name="dateFrom"
                                    id="dateFrom"
                                    onChange={this.selectedDateTo}
                                    placeholder="From" /> */}

                            {/* <Col sm="12" md={{ size: 8, offset: 0 }} >
                            </Col> */}

                            <Col sm="12" md={{ size: 8, offset: 4 }} style={{ clear: "both", paddingRight: "23.5px" }}>
                                <br />
                                <a style={{ paddingLeft: "258px", paddingTop: "70px" }}>
                                    <button type="submit" onClick={this.applyFilter} className="btn btn-lg" style={{ backgroundColor: '#E60000', color: '#fff', paddingLeft: 15, paddingRight: 15, paddingTop: 0, paddingBottom: 0, float: "right" }} >Apply</button>

                                </a>
                            </Col>

                        </FormGroup>
                    </form>
                </Card>
                <hr />

                <div style={{ paddingRight: "20px", height: "1px" }}>
                    <ReactHTMLTableToExcel
                        className="btn btn-outline-danger btn-sm m-0  waves-effec float-right"
                        table="idforSubscription"
                        filename="PRC-ReportExcel"
                        sheet="Sheet"
                        buttonText="Export excel"
                        
                    />

                </div>
                <br /><br />
                <SubscriptionDataTables id="idforSubscription" posts={this.state.ProductData} />

                {/*                 
                <table id="dtBasicExample" class="table table-striped table-bordered table-sm table-responsive-sm table-hover" cellspacing="5" width="100%">
                    <thead>
                        <tr>
                            <th class="th-sm" scope="row"></th>
                            <th class="th-sm" scope="row">Position</th>
                            <th class="th-sm" scope="row">Office</th>
                            <th class="th-sm" scope="row">Age</th>
                        </tr>
                    </thead>
                    <tbody>              {
                        this.state.ProductData.map((p, index) => {
                            return <tr key={index}>
                                <td>
                                    {p.id}
                                </td>
                                <td >{p.name}</td>
                                <td >{p.username}</td>
                                <td style={{ paddingRight: "114px" }}>{p.email}</td>
                            </tr>
                        })
                    }
                    </tbody>
                </table> */}
            </div>
        )
    }
}

export default ExportExcel  